import { useState } from 'react';
import { get, useForm } from 'react-hook-form';
import { useDispatch, useSelector } from 'react-redux';
import { Link, Navigate } from 'react-router-dom';
import * as yup from 'yup';
import { yupResolver } from '@hookform/resolvers/yup';
import { AuthFormError, GreenButton } from '../../components';
import { useResetForm } from '../../hooks';
import { setUser } from '../../actions';
import { selectUserRole } from '../../selectors';
import { ROLE } from '../../constants';
import { request } from '../../utils/request';
import styled from 'styled-components';
import { faArrowRight, faLock, faUser } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

const regFormSchema = yup.object().shape({
	login: yup
		.string()
		.required(' ')
		.matches(/^\w+$/, '  .     ')
		.min(3, '  .  3 ')
		.max(15, '  . 15 '),
	password: yup
		.string()
		.required(' ')
		.matches(
			/^[\w#%]+$/,
			'  .  ,    #, %',
		)
		.min(6, '  .  6 ')
		.max(30, '  . 30 '),
	passCheck: yup
		.string()
		.required('  ')
		.oneOf([yup.ref('password'), null], '  '),
});

const RegistrationContainer = ({ className }) => {
	const {
		register,
		reset,
		handleSubmit,
		formState: { errors },
		getValues,
	} = useForm({
		defaultValues: {
			login: '',
			password: '',
			passCheck: '',
		},
		resolver: yupResolver(regFormSchema),
	});

	const [serverError, setServerError] = useState(null);

	const dispatch = useDispatch();

	const roleId = useSelector(selectUserRole);

	useResetForm(reset);

	const onSubmit = ({ login, password, passCheck }) =>
		console.log(login, password, passCheck);

	const formError =
		errors?.login?.message || errors?.password?.message || errors?.passCheck?.message;
	const errorMessage = formError || serverError;

	if (roleId !== ROLE.GUEST) {
		return <Navigate to="/" />;
	}
	console.log(getValues());

	return (
		<div className={className}>
			<h2></h2>
			<form onSubmit={handleSubmit(onSubmit)}>
				<div className="input-auth">
					<FontAwesomeIcon icon={faUser} color="gray" />
					<input
						type="text"
						placeholder="..."
						{...register('login', {
							onChange: () => setServerError(null),
						})}
					/>
				</div>
				<div className="input-auth">
					<FontAwesomeIcon icon={faLock} color="gray" />
					<input
						type="password"
						placeholder="..."
						{...register('password', {
							onChange: () => setServerError(null),
						})}
					/>
				</div>
				<div className="input-auth">
					<FontAwesomeIcon icon={faLock} color="gray" />
					<input
						type="password"
						placeholder=" ..."
						{...register('passCheck', {
							onChange: () => setServerError(null),
						})}
					/>
				</div>
				<GreenButton place={20} right={true} icon={faArrowRight} type={'submit'}>
					
				</GreenButton>
				{errorMessage && <AuthFormError>{errorMessage}</AuthFormError>}
			</form>
		</div>
	);
};

export const Registration = styled(RegistrationContainer)`
	display: flex;
	align-items: center;
	flex-direction: column;

	.input-auth {
		display: flex;
		align-items: center;
		border: 1px solid #ebe5e5;
		border-radius: 10px;
		padding: 0 15px;
		margin-bottom: 20px;
	}

	input {
		border: none;
		outline: none;
		padding: 13px;
		font-size: 16px;
	}

	& > form {
		display: flex;
		flex-direction: column;
		width: 260px;
	}
`;
